/* src/styles/App.css */

body {
    font-family: 'Arial', sans-serif;
    background-color: #f5f5f5;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
  }
  
  .container {
    width: 80%;
    background: white;
    padding: 20px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-bottom: 2px solid #ddd;
  }
  
  .brand-title {
    font-size: 28px;
    font-weight: bold;
  }
  
  .nav-bar ul {
    list-style: none;
    display: flex;
    gap: 15px;
  }
  
  .nav-bar li {
    cursor: pointer;
    font-size: 16px;
  }
  
  .search-section {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 20px 0;
  }
  
  .search-section input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .search-section button {
    background-color: black;
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
  }
  
  .product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
  }
  
  .product-card {
    background: white;
    padding: 10px;
    text-align: center;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .product-image {
    width: 100%;
    height: auto;
  }
  
  .order-form {
    margin-top: 30px;
    padding: 20px;
    border-top: 2px solid #ddd;
  }
  
  .order-form form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .order-form input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .order-form button {
    background-color: black;
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
  }
  
  .order-summary {
    margin-top: 20px;
    padding: 15px;
    background: #f9f9f9;
    border-radius: 5px;
  }
  
  .order-summary h3 {
    margin-bottom: 10px;
  }
  
  .order-item {
    border-bottom: 1px solid #ddd;
    padding: 5px 0;
  }
  